














































































































.row-wrap {
  position: relative;
  border: 1px dotted transparent;
  .grid-wrap {
    border: 1px dotted #409eff;
    position: relative;
    padding: 0 12px;
    &:hover {
      border: 1px solid #409eff;
      .grid-col {
        border-style: solid;
      }
    }
    &.active {
      border: 3px solid #409eff;
      /*.grid-col {*/
      /*  &:not(:last-child) {*/
      /*    border-right: 3px solid #409eff;*/
      /*    &:hover {*/
      /*      border-right: 3px solid #409eff;*/
      /*    }*/
      /*  }*/
      /*}*/
    }
    .grid-col {
      border: 1px dotted #409eff;
      .grid-control-wrap {
        height: 100%;
        min-height: 80px;
        padding: 5px;
      }
    }
  }

  .control-item {
    position: relative;
    border: 1px solid transparent;
    &:hover {
      border: 1px dotted rgba(53,201,160,1);
    }
    &.active {
      border: 3px solid rgba(53,201,160,1);
    }
  }
}
.active {
  border-style: solid;
}
.ghost {
  border: 2px solid red;
}
