.custom-resizer {
  width: 100%;
  min-height: 500px;
  height: calc(100% - 20px);
}
.custom-resizer > .pane {
  text-align: left;
  padding: 15px;
  /*height: 100%;*/
  min-height: 700px;
  border: 1px solid #ccc;
  overflow-y: scroll;
}
.custom-resizer > .multipane-resizer {
  margin: 0;
  left: 0;
  margin-top: 20%;
  position: relative;
}
.custom-resizer > .multipane-resizer:before {
  display: block;
  content: "";
  width: 3px;
  height: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -20px;
  margin-left: -1.5px;
  border-left: 1px solid #ccc;
  border-right: 1px solid #ccc;
}
.custom-resizer > .multipane-resizer:hover:before {
  border-color: #999;
}
.controls-list {
  width: 25%;
}
.form-view {
  width: 50%;
}
