.row-wrap {
  position: relative;
  border: 1px dotted transparent;
}
.row-wrap .grid-wrap {
  border: 1px dotted #409eff;
  position: relative;
  padding: 0 12px;
}
.row-wrap .grid-wrap:hover {
  border: 1px solid #409eff;
}
.row-wrap .grid-wrap:hover .grid-col {
  border-style: solid;
}
.row-wrap .grid-wrap.active {
  border: 3px solid #409eff;
  /*.grid-col {*/
  /*  &:not(:last-child) {*/
  /*    border-right: 3px solid #409eff;*/
  /*    &:hover {*/
  /*      border-right: 3px solid #409eff;*/
  /*    }*/
  /*  }*/
  /*}*/
}
.row-wrap .grid-wrap .grid-col {
  border: 1px dotted #409eff;
}
.row-wrap .grid-wrap .grid-col .grid-control-wrap {
  height: 100%;
  min-height: 80px;
  padding: 5px;
}
.row-wrap .control-item {
  position: relative;
  border: 1px solid transparent;
}
.row-wrap .control-item:hover {
  border: 1px dotted #35c9a0;
}
.row-wrap .control-item.active {
  border: 3px solid #35c9a0;
}
.active {
  border-style: solid;
}
.ghost {
  border: 2px solid red;
}
